import React from 'react'

export const Data = React.memo(({information}) => {

    const {Country, TotalConfirmed, TotalDeaths, NewConfirmed, NewDeaths} = information;
    
    return (
        <div>
            <h2>Country: {Country}</h2>
            <h2>Total confirmed: {TotalConfirmed}</h2>
            <h2>Total deaths: {TotalDeaths}</h2>
            <h2>New confirmed today: {NewConfirmed}</h2>
            <h2>New deaths today: {NewDeaths}</h2>
        </div>
    )
});